.App{
  width: 90%;
  padding: 2%;
  gap: 5%;
}
body{
  background-color: rgb(245,255,255);
}
.adduser{
  display: flex;
  flex-direction: column;
  background-color: rgb(109,215,253);
  padding: 3%;
  border-radius: 8px;
  height: auto;
  width: 50%;
  box-shadow: 1px 2px 2px 1px rgb(223, 223, 223);
  transition: all 0.3s ease-in-out;
}
.adduser:hover{
  border: 2px solid rgb(0,147,203);
  transition: all 0.5s ease-in-out;
  box-shadow: 1px 2px 20px 1px rgb(0,147,203);

}
.adduser input{
  margin-bottom: 1%;
  border-radius: 5px;
  border: 1px solid rgb(0,147,203);
  padding: 1%;
  outline: 1px solid rgb(0,90,205);
  transition: all 0.3s ease-in-out;
  box-shadow: 1px 2px 2px 1px rgb(42, 71, 216)
}
.adduser input:hover{
  transition: all 0.3s ease-in-out;
  padding-bottom: 3%;
}
.adduser button{
  margin-top: 3%;
  border-radius: 5px;
  border: 1px solid rgb(0,147,203);
  background-color: rgb(190,240,255);
  font-size: 20px;
  color: black;
  cursor: pointer;
  padding: 1%;
  transition: all 0.3s ease-in-out;
}
.adduser button:hover{
  transition: all 0.3s ease-in-out;
  background-color: rgb(0,147,203);
}
.adduser label{
  font-size: 17px;
  font-weight: 600;
  margin-bottom: 2%;
  margin-top: 2%;
}
.card-boxs{
  display: flex;
  width: 100%;
  flex-basis: 33%;
  flex-wrap: wrap;
  gap: 5%;
}
.card-boxs span{
  display: flex;
  width: 100%;
  justify-content: flex-end;
  gap: 7%;
}
.card-boxs span img{
  width: 30px;
  height: 30px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.card-boxs span img:hover{
  animation: vibrate 0.3s linear infinite;
}
@keyframes vibrate {
  0% { transform: translate(0); }
  20% { transform: translate(-1.5px, 1.5px); }
  40% { transform: translate(-1.5px, -1.5px); }
  60% { transform: translate(1.5px, 1.5px); }
  80% { transform: translate(1.5px, -1.5px); }
  100% { transform: translate(0); }
}

.cards{
  width: 25%;
  margin-top: 3%;
  display: flex;
  flex-direction: column;
  height: auto;
  box-shadow: 1px 2px 4px 2px rgb(109,215,253);
  border-radius: 8px;
  padding: 2%;
  transition: all 0.3s ease-in-out;
  background-color: rgb(190,240,255);
}
.cards:hover{
  transition: all 0.3s ease-in-out;
  box-shadow: 1px 2px 6px 3px rgb(109,215,253);
  border: 1px solid rgba(0,147,203,0.6);

}
.cards h1{
  font-size: 20px;
  display: flex;
  align-items: center;
}
.cards h1 img{
  margin-left: 5%;
}
.edituser{
  display: flex;
  flex-direction: column;
}
.edituser input{
  margin-bottom: 1%;
  border-radius: 5px;
  border: 1px solid rgb(0,147,203);
  padding: 1%;
  outline: 1px solid rgb(0,90,205);
  transition: all 0.3s ease-in-out;
  box-shadow: 1px 2px 2px 1px rgb(42, 71, 216)
}
.edituser input:hover{
  transition: all 0.3s ease-in-out;
  padding-bottom: 3%;
}
.edituser button{
  margin-top: 3%;
  border-radius: 5px;
  border: 1px solid rgb(0,147,203);
  background-color: rgb(190,240,255);
  font-size: 20px;
  color: black;
  cursor: pointer;
  padding: 1%;
  transition: all 0.3s ease-in-out;
}
.edituser button:hover{
  transition: all 0.3s ease-in-out;
  background-color: rgb(0,147,203);
}
.edituser label{
  font-size: 17px;
  font-weight: 600;
  margin-bottom: 2%;
  margin-top: 2%;
}
@media only screen and (max-width: 500px) {
  .cards h1{
    font-size: 14px;
  }
  .cards{
    width: 80%;
  }
}